.section-padding {
  padding: 4rem 4rem;
}

.footer {
  margin-top: 9rem;
  background-color: hsl(183, 100%, 15%);
}

.sb_footer {
  display: flex;
  flex-direction: column;
}

.footer-links {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  text-align: left;
  margin-bottom: 2rem;
}

.footer-section-links-div {
  width: 150px;
  margin: 1rem;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  color: white;
}

a {
  color: rgb(175, 175, 179);
  text-decoration: none;
}
.social-media {
  display: flex;
  flex-direction: row;
}

.footer-section-links-div h4 {
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 0.9rem;
}

.footer-section-links-div p {
  font-size: 12px;
  line-height: 15px;
  margin: 0.5rem 0;
  cursor: pointer;
}

.footer-below {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 0.2rem;
}

.footer-below-links {
  display: flex;
  flex-direction: row;
}
.footer-below-links p {
  font-size: 13px;
  line-height: 15px;
  margin-left: 2rem;
  color: white;
  font-weight: 600;
}

hr {
  color: white !important;
  width: 100%;
}

.copyright {
  font-size: 13px;
  list-style: 15px;
  font-weight: 600;
  color: white;
}

@media screen and (max-width: 850px) {
  .footer-links div {
    margin: 1rem 0;
  }

  .footer-below {
    flex-direction: column;
    justify-content: left;
  }
  .footer-below-links {
    flex-direction: column;
  }

  .footer-below-links p {
    margin-left: 0rem;
    margin-top: 1rem;
  }
}

@media screen and (max-width: 400px) {
}
